body {
  overflow-x: hidden;
  padding: 0;
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

input[type='button']{
  &:focus, &:active{
    outline: none;
    border: 0;
  }
  &:active:focus{
    border: 0;
    outline: none;
  }
}
.slick-slide {
  &:focus {
    outline: none;
  }
}

.slick-next {
  display: block;
  height: 20px;
  width: 20px;
  top: 40% !important;

  &:before {
    color: $theme_black !important;
    opacity: 1 !important;
    content: url('../../images/next.png') !important;
  }
}
.border-div{
  height: 1px;
  background-color: $gray-color;
  width: 100%;
}

.join-meetup{
  font-size: 32px;
  font-weight: bold;
  color: $theme_black;
  text-align: center;
}
.meetup{
  color: #EA1353;
  text-align: center;
  font-size: 20px;
  font-style: italic;
}
.facebook{
  text-align: center;
  margin-bottom: 100px;
}
.image-block{
  img{
    max-width: 100%;
  }
}
.tour-card{
  min-height: 410px;
}
select{
  &.filter-result{
    margin-bottom: 20px;
  }
}

.single-error-message{
  text-align: center;
    display: block;
    color: red;
    font-weight: 500;
    padding: 0px;
    /* margin-bottom: 9px; */
    margin-top: -29px;
}
.error-message{
  color: red;
    font-weight: 600;
    font-size: 21px;
    background: #ccc;
    padding: 30px 10px;
    border-left: 12px solid red;
    width: 500px;
    text-align: center;
    margin: 20px auto;
}
